let root = {};
export const date = new Date(); //new Date("December 16, 2016 11:13:00");//
export const time = date.getSeconds();
export const month = date.getMonth();
export const day = date.getDate();
export const xmas = month == 11 || (month == 0 && day <= 10); //between dec 1 and jan 10
export const hallow = month == 9 && day >= 10; //between oct 10 and oct 31	
export const viskey = 'visibility';
export const visval = 'visible';
export const xkey = 'x';
export const ykey = 'y';
export const hkey = 'height';
export const wkey = 'width';
const imagePaths = {};
const darkModeKey = "darkmode-text";
import hexRgb from 'hex-rgb';
export const svgNamespace = "http://www.w3.org/2000/svg";
export function applySavedDarkMode() {
    let dark = isDarkModeEnabled();
    applyDarkMode(dark);
    let newest = document.getElementById("darkmode-toggle");
    if (newest != null)
        newest.addEventListener("click", function () { toggleDarkMode(); });
}
export function loadCachedVariable(id) {
    try {
        return window.localStorage.getItem(id);
    }
    catch (e) {
        console.log("Error thrown accessing local storage: " + e?.toString());
        //alert("Failed to load stored settings for this page ("+e?.toString()+"). Are you blocking cookies?");
    }
}
export function loadCachedVariableAsBoolean(id) {
    let ret = loadCachedVariable(id);
    return ret != null ? ret === "true" : false;
}
export function loadCachedVariableAsBooleanOrNull(id) {
    let ret = loadCachedVariable(id);
    return ret != null ? ret === "true" : null;
}
export function saveCachedVariable(id, value) {
    try {
        window.localStorage.setItem(id, value);
    }
    catch (e) {
        console.log("Error thrown accessing local storage: " + e?.toString());
        alert("Could not access setting storage (see console for more info); your settings will not be preserved. Are you blocking all cookies?");
    }
}
function toggleDarkMode() {
    let has = isDarkModeEnabled();
    saveCachedVariable(darkModeKey, (!has).toString());
    applyDarkMode(!has);
}
export function isDarkModeEnabled() {
    return loadCachedVariableAsBoolean(darkModeKey);
}
function applyDarkMode(active) {
    console.log("Applying dark mode theming: " + active);
    applyDarkModeToElement(active, document.getElementById("textarea"));
    applyDarkModeToElement(active, document.getElementById("toc_container"));
    let elems = document.getElementsByClassName("core-body");
    for (var i = 0; i < elems.length; i++) {
        applyDarkModeToElement(active, elems[i]);
    }
    elems = document.getElementsByClassName("other-project-body");
    for (var i = 0; i < elems.length; i++) {
        applyDarkModeToElement(active, elems[i]);
    }
    window.document.dispatchEvent(new Event("resize", { bubbles: true, cancelable: true }));
}
function applyDarkModeToElement(active, elem) {
    if (elem == null)
        return;
    if (active) {
        elem.classList.add(darkModeKey);
    }
    else {
        elem.classList.remove(darkModeKey);
    }
}
export function getRandomMapKey(obj) {
    let keys = Object.keys(obj);
    return keys[keys.length * Math.random() << 0];
}
export function invokeMethod(m, name) {
    let idx = Object.keys(m).indexOf(name);
    if (idx >= 0)
        return Object.values(m)[idx]();
}
export function injectAtlasNode(elem, x, y, game, id, w, h, blend = null) {
    let forg = document.createElementNS(svgNamespace, "foreignObject");
    forg.setAttribute(wkey, w.toString());
    forg.setAttribute(hkey, h.toString());
    forg.setAttribute(xkey, x.toString());
    forg.setAttribute(ykey, y.toString());
    if (blend)
        forg.setAttribute("style", "mix-blend-mode: " + blend + ";");
    let div = document.createElement("div");
    div.setAttribute("xmlns", "http://www.w3.org/1999/xhtml");
    div.className = "tile-" + game + "-" + id;
    forg.appendChild(div);
    elem.appendChild(forg);
    return forg;
}
