export function hexToRGB(hex) {
    let ret = hexRgb(hex.toLowerCase().replace("0x", "#"));
    return { r: ret.red, g: ret.green, b: ret.blue };
}
export function RGBtoHSV(r, g, b) {
    let max = Math.max(r, g, b), min = Math.min(r, g, b), d = max - min, h = 0, s = (max === 0 ? 0 : d / max), v = max / 255;
    switch (max) {
        case min:
            h = 0;
            break;
        case r:
            h = (g - b) + d * (g < b ? 6 : 0);
            h /= 6 * d;
            break;
        case g:
            h = (b - r) + d * 2;
            h /= 6 * d;
            break;
        case b:
            h = (r - g) + d * 4;
            h /= 6 * d;
            break;
    }
    return {
        h: h * 360,
        s: s,
        v: v
    };
}
export function fitBCG(elem, scale) {
    //elem.style.transform = "scale("+scale+", "+scale+") translate("+rows*w/4+"px, "+cols*h*0/4+"px)";
    elem.style.transformOrigin = "top left";
    elem.style.transform = "scale(" + scale + ")";
}
export function absDiff(a, b) {
    return Math.abs(Math.abs(a) - Math.abs(b));
}
export function getImageRoot(folderName) {
    if (root[folderName] == null || typeof (root[folderName]) == "undefined") {
        let ret = "images/" + folderName + "/";
        let url = window.location.href;
        //console.log(url);
        let idx = url.lastIndexOf("sitecore");
        if (idx == -1 && url.includes("Generator/exported")) {
            idx = url.lastIndexOf("exported");
        }
        url = url.substring(idx + 9);
        //console.log(url);
        let levels = url.split("/").length - 1;
        for (let i = 0; i < levels; i++)
            ret = "../" + ret;
        root[folderName] = ret;
        //console.log("Calculated root "+root[folderName]);
    }
    return root[folderName];
}
export function getRandomBetween(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min);
}
export function getRandomDecimalBetween(min, max) {
    return Math.random() * (max - min) + min;
}
export function getOrCreateImgPath(img, folder) {
    if (imagePaths[img] == null) {
        imagePaths[img] = getImageRoot(folder) + img + ".png";
        console.log("Computed image path '" + imagePaths[img] + "' for image '" + img + "'");
    }
    return imagePaths[img];
}
export function setRandomRotations(elems, range) {
    const ang = getRandomBetween(-range, range);
    for (let elem of elems)
        setRotation(elem, ang);
}
export function setRandomRotation(elem, range) {
    setRotation(elem, getRandomBetween(-range, range));
}
export function setRotations(elems, ang) {
    for (let elem of elems)
        setRotation(elem, ang);
}
export function setRotation(elem, ang) {
    elem.style.transformOrigin = "center";
    elem.style.transform = elem.style.transform + " rotate(" + ang + "deg)";
}
export function mirrorXs(elems) {
    for (let elem of elems)
        mirrorX(elem);
}
export function mirrorX(elem) {
    elem.style.transformOrigin = "center";
    elem.style.transform = elem.style.transform + " scaleX(-1)";
}
export function getArrayIndex(cols, rows, x, y) {
    if (x < 0 || y < 0) {
        console.log("Negative tile position @ " + x + ", " + y + "!");
        console.trace();
        return 0;
    }
    if (x >= cols || y >= rows) {
        console.log("OOB tile position @ " + x + ", " + y + "!");
        console.trace();
        return 0;
    }
    if (x != Math.floor(x) || y != Math.floor(y)) {
        console.log("Noninteger tile position @ " + x + ", " + y + "!");
        console.trace();
        return 0;
    }
    return x + y * cols;
}
